<template lang="html">
  <div>
    <f7-actions id="editor" :opened="isEditorOpened">
    <!-- Actions Group -->
    <f7-actions-group class="editor__wrapper">
      <!-- Actions Label -->
      <f7-actions-label class="editor__header">Article Editor</f7-actions-label>
      <div class="editor__close__wrapper" @click="closeEditor">
        <span class="editor__close">+</span>
      </div>
      <div class="flex">
      <form class="editor__form">
        <div class="editor__article-card"
             :style="{'background-image': `url(${urlToImage})`}"
             >
            <!-- Article info -->
             <div class="nc-article-card__contents">
               <textarea id="editor-title" class="editor__title" v-model="title"></textarea>
               <section class="editor__article-card__details flex">
                 <input class="editor__source" type="text" name="" v-model="source">
                 <input class="editor__publishedAt" type="text" name="" v-model="publishedAt">
                 <!-- <p>{{article.source}}</p><p>{{article.publishedAt}}</p> -->
               </section>
             </div>
        </div>
        <div class="flex">
          <div class="editor__input-wrapper">
            <div class="flex">
              <i class="fa fa-desktop editor__icon" aria-hidden="true"></i>
              <input class="editor__input" type="text" name="" v-model="url">
            </div>
            <div class="flex">
              <i class="fa fa-picture-o editor__icon" aria-hidden="true"></i>
              <input class="editor__input" type="text" name="" v-model="urlToImage">
            </div>
            <div class="flex">

              <textarea class="editor__input__description" v-model="description"></textarea>
            </div>
          </div>
        </div>
      </form>
    </div>
        <f7-button class="editor__button" @click="closeEditor">done</f7-button>
    </f7-actions-group>
    <!-- Another Group -->
  </f7-actions>

  </div>
</template>

<script>
export default {
  props: {
    isEditorOpened: Boolean,
    article: Object
  },
  methods: {
    closeEditor: function() {
      window.f7.closeModal();
      let self = this;
      setTimeout(function(){self.$emit("closeEditor") }, 300);
    }
  },
  data: function() {
    return {
      title: this.article.title,
      author: this.article.author,
      source: this.article.source,
      url: this.article.url,
      urlToImage: this.article.urlToImage,
      description: this.article.description,
      publishedAt: this.article.publishedAt
    }
  }
}
</script>



